  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between; }

.flex-box {
  display: flex;
  align-items: center;
  justify-content: space-between; }

/* Для Chrome */
input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
