/*--------------------------------------------------------------
# Radiobutton * Кастомный radiobutton
--------------------------------------------------------------*/
.radiobutton {
  width: fit-content;
  margin-top: 10px;
  display: flex;
  align-items: flex-start;
  font-size: 15px; }
  .radiobutton input[type="radio"]:checked,
  .radiobutton input[type="radio"]:not(:checked) {
    position: absolute;
    left: -9999px; }
  .radiobutton input[type="radio"]:checked + span,
  .radiobutton input[type="radio"]:not(:checked) + span {
    display: inline-block;
    position: relative;
    padding-left: 28px;
    line-height: 20px;
    cursor: pointer; }
  .radiobutton input[type="radio"]:checked + span:before,
  .radiobutton input[type="radio"]:not(:checked) + span:before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 18px;
    height: 18px;
    box-sizing: border-box;
    border: 2px solid #001535;
    background-color: transparent; }
  .radiobutton input[type="radio"]:checked + span:before,
  .radiobutton input[type="radio"]:not(:checked) + span:before {
    border-radius: 100%; }
  .radiobutton input[type="radio"]:checked + span:after,
  .radiobutton input[type="radio"]:not(:checked) + span:after {
    content: "";
    position: absolute;
    transition: all 0.2s ease; }
  .radiobutton input[type="radio"]:checked + span:after,
  .radiobutton input[type="radio"]:not(:checked) + span:after {
    left: 4px;
    top: 4px;
    width: 10px;
    height: 10px;
    border-radius: 100%;
    background-color: #001535; }
  .radiobutton input[type="radio"]:not(:checked) + span:after {
    opacity: 0; }
  .radiobutton input[type="radio"]:checked + span:after {
    opacity: 1; }

